  .button {
    background-color: #333;
    color: #fff;
    padding: 0.5rem 1rem;
    /* border: none; */
    border-radius: 0.25rem;
    font-size: 1.1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #555;
  }
  
  .headerLogo {
    height: 40px;
    width: 70px;
  }

  /* .tags-container {
    display: flex;
    flex-wrap: wrap;
