   */


   .input-box {
    height: 64px;
    border: 1.5px solid rgba(23, 42, 55, 1);
    width: 400px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 10px;
    font-size: 20px;
    color: #333;
    outline: none;
    transition: height 0.2s ease-in-out;
    box-sizing: border-box;
    overflow-y: auto;
    border-radius: 5px;
  }
  
  .tags-container {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
    margin-right: -4px;
    width: 368px;
  }


  .input-box::placeholder {
    font-weight: 100;
    color: #999999;
    font-size: 17px;
  }
  

  @media (max-width: 768px) {
    .input-box {
      width: 320px;
      font-size: 15px;
      padding: 4px;
    }
  }

  @media (max-width: 768px) {
    .input-box::placeholder {
      font-size: 15px;
    }
  }


  @media (max-width: 768px) {
    .tags-container {
      width: 297px;
      font-size: 12px;
    }
  }
  
  .tag {
    display: flex;
    align-items: center;
    background-color: rgba(23, 42, 55, 1);
    color: #fff;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 14px;
    margin: 0.3rem;
  }
  
  .tag button {
    margin-left: 8px;
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    outline: none;
  }
  
  .tag button:hover {
    text-decoration: underline;
  }
  
  .input-box:focus-within {
    border-color: #333;
  }
  
  .input-box:focus-within .tags-container {
    margin-bottom: 8px;
  }
  
  .input-box:focus-within .tags-container .tag {
    background-color: #333;
  }
  


