/* For mobile screens */
@media only screen and (max-width: 425px) {
  .allInputs {
    width: 80%;
  }
  .text-with-lines::before,
  .text-with-lines::after {
    width: 9.4rem; /* Decrease this value to shorten the line length */
  }
}

/* For mobile screens */
@media only screen and (max-width: 360px) {
  .allInputs {
    width: 70%;
  }
  .text-with-lines::before,
  .text-with-lines::after {
    width: 6.5rem; /* Decrease this value to shorten the line length */
  }
}
